@import 'compass/css3';

.jq-selectbox {
  vertical-align: middle;
  cursor: pointer;
  width: 100%;
  max-width: 296px;

  &:hover {
    .jq-selectbox__trigger-arrow {
      @include opacity(1);
    }
  }

  &.focused {
    .jq-selectbox__select {
      border: 1px solid #a9ddf0;
      @include box-shadow(0 0 5px rgba(90, 194, 231, .5));
    }
  }

  &.disabled {
    .jq-selectbox__select {
      border-color: #ccc;
      background: #f5f5f5;
      @include box-shadow(none);
      color: #888;
    }

    .jq-selectbox__trigger-arrow {
      @include opacity(.3);
    }
  }

  &.placeholder {
    color: #888;
  }

  ul {
    margin: 0;
    padding: 0;
    border-top: 1px solid #fff;
  }

  li {
    min-height: 18px;
    padding: 9px 10px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -o-user-select: none;
    white-space: nowrap;
    color: #231f20;
    border-bottom: 1px solid #e2e2e2;

    &.selected {
      background-color: #f2f2f2;
      color: #333;
    }

    &:hover {
      background-color: #f2f2f2;
      color: #333;
    }

    &.disabled {
      color: #aaa;

      &:hover {
        background: none;
      }
    }

    &.optgroup {
      font-weight: bold;

      &:hover {
        background: none;
        color: #231f20;
        cursor: default;
      }
    }

    &.option {
      padding-left: 25px;
    }
  }
}

.jq-selectbox__select {
  height: 40px;
  padding: 0 45px 0 50px;
  border: 1px solid #ededed;
  @include border-radius(4px);
  background-color: #fff;
  color: #6b6b6b;
  font: 14px/40px Arial, sans-serif;

  &:hover {
    background-color: #fff;
  }

  &:active {
    background: #fff;
  }
}

.jq-selectbox__select-text {
  display: block;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.jq-selectbox__trigger {
  position: absolute;
  top: 0;
  right: 0;
  width: 36px;
  height: 100%;
}

.jq-selectbox__trigger-arrow {
  position: absolute;
  top: 18px;
  right: 14px;
  width: 0;
  height: 0;
  overflow: hidden;
  border-top: 5px solid #000;
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
  @include opacity(.3);
}

.jq-selectbox__dropdown {
  top: 41px;
  width: 100%;
  @include box-sizing(border-box);
  margin: 0;
  padding: 0;
  border: 1px solid #a9ddf0;
  @include border-radius(0 0 4px 4px);
  background: #fff;
  @include box-shadow(0 0 5px rgba(90, 194, 231, .5));
  font: 14px/18px Arial, sans-serif;
}

.jq-selectbox__search {
  margin: 5px;

  input {
    @include box-sizing(border-box);
    width: 100%;
    margin: 0;
    padding: 5px 27px 6px 8px;
    outline: none;
    border: 1px solid #ccc;
    @include border-radius(3px);
    background: url('data:image/png;base64,') no-repeat 100% 50%;
    @include box-shadow(inset 1px 1px #f1f1f1);
    color: #333;
    -webkit-appearance: textfield;

    &::-webkit-search-cancel-button,
    &::-webkit-search-decoration {
      -webkit-appearance: none;
    }
  }
}

.jq-selectbox__not-found {
  margin: 5px;
  padding: 5px 8px 6px;
  background: #f0f0f0;
  font-size: 13px;
}

body {
  background-color: #ccc;
  color: #232323;
  font: normal 14px/24px 'Arial', sans-serif;
}

select {
  width: 100%;
  height: 42px;
  border: 1px solid #ededed;
  @include border-radius(4px);
  background-color: #fff;
  color: #6b6b6b;
  font: 14px/40px Arial, sans-serif;

  &:focus {
    outline: 0;
    border-color: #a9ddf0;
    @include box-shadow(0 0 5px rgba(90, 194, 231, .5));
  }
}

.input-icon-block {
  position: relative;
}

.input-icon {
  display: block;
  width: 59px;
  height: 40px;
  position: absolute;
  top: 1px;
  left: 1px;
  z-index: 2;
  cursor: pointer;
  background: #5ac2e7 none no-repeat center center;
  border-right: 1px solid #fff;
  @include border-radius(3px 0 0 3px);
}

.change-credit-card {
  margin: 4px auto;
  @include box-shadow(0 1px 4px rgba(0, 0, 0, .25));
}

.payment-card {
  width: 300px;
  margin: 200px auto 30px;
  padding: 20px;
  border: 1px solid #ececec;
  border-top: 5px solid #5ac2e7;
  background-color: #f9f9f9;

  select,
  .jq-selectbox__select {
    padding-left: 75px;
  }
}

.credit-cards-icon {
  margin: 0 0 10px;
  padding: 0;
  list-style: none;
  overflow: hidden;

  li {
    cursor: pointer;
    float: left;
    margin-right: 13px;
    @include border-radius(2px);
    @include box-shadow(0 1px 4px rgba(0, 0, 0, .25));
    &:last-child {
      margin-right: 0;
    }
  }
}

.mc,
.visa,
.ae,
.ec,
.jcb {
  width: 48px;
  height: 33px;
  display: block;
  background: url('data:image/png;base64,') no-repeat;
}

.mc {
  background-position: 0 0;
}

.visa {
  background-position: -49px 0;
}

.ae {
  background-position: -98px 0;
}

.ec {
  background-position: -147px 0;
}

.jcb {
  background-position: -196px 0;
}
